import Vue from 'vue'
import App from './App.vue'
import VueRouter from "vue-router";
import VueResource from 'vue-resource'

//debug
Vue.config.debug = true;

Vue.use(VueRouter);
Vue.use(VueResource);

import Index from './subpage/index.vue';
import menuone from './subpage/menuone.vue';
import notfound from './subpage/notfound.vue';
import onesub from './subpage/subone.vue';
const router = new VueRouter({
	base: __dirname,
	routes: [{
		path: '/',
		redirect: "/index",
		children: []
	}, {
		path: '/index',
		component: Index
	}, {
		path: '/one',
		component: menuone,
		children: []
	}, {
		path: "/onesub",
		component: onesub,
	}, {
		path: "*",
		component: notfound
	}]
});
let [routerHistory, appVue] = [
	[]
];
/*
 router.beforeEachbefore
 to:Rounte:
 fromRounte
 nextFunctionresolvenext
  nextcnfirmed
  nextfalseurlurlfrom
  next'/')next({path:'/'})
  nextresolved
  afterEachafternext
 * */
router.beforeEach((to, from, next) => {
	let index = routerHistory.indexOf(to.fullPath)
	// if (to.path === '/') {
	//   next('/TabHome')
	//   return false
	// }
	// if (to.path === '/') {
	//   routerHistory = []
	// }
	if(index !== -1) {
		routerHistory = routerHistory.slice(0, index + 1)
		console.log('')
		appVue.tName = 'slide-prev'
	} else {
		routerHistory.push(to.fullPath)
		console.log('')
		if(appVue) {
			appVue.tName = 'slide-next'
		}
	}
	global.routerHistory = routerHistory
	console.log(routerHistory)
	next()
});
appVue = new Vue({
	el: '#app',
	router,
	data: {
		tName: 'slide-next',
		text: "123",
	},
	template: '<App v-bind:tName="tName" v-bind:text="text"/>',
	components: { App },
});


